import { Group } from '@visx/group';
import { BarGroupHorizontal, Bar } from '@visx/shape';
import { AxisLeft, AxisBottom } from '@visx/axis';
import { scaleBand, scaleLinear, scaleOrdinal } from '@visx/scale';
import { LegendOrdinal, LegendItem } from '@visx/legend';

const dataset = [
  {
    race: 'AMERICAN INDIAN/ALASKAN N',
    '2013': 818,
    '2020': 17,
  },
  {
    race: 'WHITE',
    '2013': 1145,
    '2020': 856
  },
  {
    race: 'ASIAN / PACIFIC ISLANDER',
    '2013': 6845,
    '2020': 219
  },
  {
    race: 'BLACK HISPANIC',
    '2013': 12271,
    '2020': 829,
  },
  {
    race: 'OTHER/UNKNOWN',
    '2013': 23664,
    '2020': 206,
  },
  {
    race: 'WHITE HISPANIC',
    '2013': 42659,
    '2020': 2050,
  },
  {
    race: 'BLACK',
    '2013': 104449,
    '2020': 5367,
  }
];

const keys = Object.keys(dataset[0]).filter((d) => d !== 'race');

function max(arr, fn) {
  return Math.max(...arr.map(fn));
}

// accessors
const getRace = (d) => d.race;

// scales
const raceScale = scaleBand({
  domain: dataset.map(getRace),
  padding: 0.2
});

const yearScale = scaleBand({
  domain: keys,
  padding: 0.1
});

const stopAndFriskScale = scaleLinear({
  domain: [0, max(dataset, (d) => max(keys, (key) => Number(d[key])))],
});

const colorScale = scaleOrdinal({
  range: ['#0000ff', '#ff0000'],
  domain: keys,
});

function getBars(barGroup){
  return barGroup.bars.map((bar) => {
    return <>
      <Bar
        key={`${barGroup.index}-${bar.index}-${bar.key}`}
        x={bar.x}
        y={bar.y}
        width={bar.width}
        height={bar.height}
        fill={bar.color}
        rx={4}
      />
      <text
        key={`text-${barGroup.index}-${bar.index}-${bar.key}`}
        x={bar.width + 10}
        y={bar.y}
        dy={14}
        fontSize={10}
      >
        {bar.value}
      </text>
    </>
  })
}

const margin = { top: 20, right: 50, bottom: 20, left: 200 };

const BarGroup = ({height, width}) => {
  const xMax = width - margin.left - margin.right;
  const yMax = height - margin.top - margin.bottom;

  raceScale.rangeRound([0, yMax]);
  yearScale.rangeRound([0, raceScale.bandwidth()]);
  stopAndFriskScale.rangeRound([0, xMax]);

  return (
    <div style={{width: width, height: height, margin: '0 auto'}}>
    <svg width={width} height={height} style={{overflow: 'visible'}}>
      <Group top={margin.top} left={margin.left}>
        <BarGroupHorizontal
          data={dataset}
          keys={keys}
          width={xMax}
          y0={getRace}
          y0Scale={raceScale}
          y1Scale={yearScale}
          xScale={stopAndFriskScale}
          color={colorScale}
        >
          {
            (barGroups) => barGroups.map((barGroup) => {
              return (
                <Group
                  key={`bar-group-horizontal-${barGroup.index}-${barGroup.y0}`}
                  top={barGroup.y0}
                >
                  {getBars(barGroup)}
                </Group>
              )
            })
          }
        </BarGroupHorizontal>
        <AxisLeft 
          scale={raceScale}
        />
        <AxisBottom 
          scale={ stopAndFriskScale }
          top={ yMax }
          tickFormat={(tick) => tick.toLocaleString("en-US")}
        />
      </Group>
    </svg>
    <LegendOrdinal scale={colorScale}>
          {
            (labels) => {
              return ( 
              <div style={{ marginTop: '1rem', display: 'flex', flexDirection: 'row', justifyContent: 'flex-end', fontSize: `16px`}}>
                {
                  labels.map((label, i) => {
                    return (
                      <LegendItem key={`legend-quantile-${i}`}>
                      <div style={{marginRight: '1rem', display: 'flex', alignItems: 'center'}}>
                        <svg height={10} width={10} style={{marginRight: '.5rem'}}>
                          <rect width={10} height={20} fill={label.value}></rect>
                        </svg>
                        <div>{label.text}</div>
                      </div>
                      </LegendItem>
                    )
                  })
                }
              </div>
              )
            }
          }
        </LegendOrdinal>
    </div>
  );
};

export default BarGroup;